import React, { Component } from 'react';
import '../../index.css';
import './head.css'

class Head extends Component {
    constructor(){
        super()
        this.state = {
            loginshow: false
        }
    }

    componentDidMount(){
        
    }

    handleEnter = () =>{
        this.setState({
            loginshow: true
        })
    }
    handleLeave = () =>{
        this.setState({
            loginshow: false
        })
    }

    render() {
        let styleshow = {
            display: (this.state.loginshow)? 'block':'none'
        }
        return (
            <div className='head'>
                <div className='head-top clearfix'>
                    <div className='headnav'>
                        <h1 className='logo bg'><a href="https://music.163.com/"> </a></h1>
                        <ul className='nav'>
                            <li className='z-slt'><a href="https://music.163.com/"></a></li>
                            <li><a href="https://music.163.com/"></a></li>
                            <li><a href="https://music.163.com/"></a></li>
                            <li><a href="https://music.163.com/"></a></li>
                            <li><a href="https://music.163.com/"></a></li>
                            <li><a href="https://music.163.com/"> <i className='nav-hot'></i></a></li>
                        </ul>

                        <div className='nav-input'>
                            <input placeholder='///'/>
                        </div>

                        <div className='head-other'>
                            <a className='other' href="https://music.163.com/"></a>
                        </div>

                        <div className='head-login' onMouseEnter={this.handleEnter} onMouseLeave={this.handleLeave}>
                            <a className='login' href="https://music.163.com/"></a>
                            {/* <ul className='head-login-item' style={styleshow}> */}
                            <ul className='head-login-item'>
                                <li><a href="https://music.163.com/"><i className='icn icn-mb'></i> <em></em></a></li>
                                <li><a href="https://music.163.com/"><i className='icn icn-wx'></i><em></em></a></li>
                                <li><a href="https://music.163.com/"><i className='icn icn-qq'></i><em>QQ</em></a></li>
                                <li><a href="https://music.163.com/"><i className='icn icn-sn'></i><em></em></a></li>
                                <li><a href="https://music.163.com/"><i className='icn icn-wy'></i><em></em></a></li>
                            </ul>
                            <i className='arr' style={styleshow}></i>
                        </div>
                    </div>
                </div>
                <div className='subnav'>
                    <div className='subnav-wrap'>
                        <ul className='subnav-item'>
                            <li><a href="https://music.163.com/"></a></li>
                            <li><a href="https://music.163.com/"></a></li>
                            <li><a href="https://music.163.com/"></a></li>
                            <li><a href="https://music.163.com/"></a></li>
                            <li><a href="https://music.163.com/"></a></li>
                            <li><a href="https://music.163.com/"></a></li>
                            <li><a href="https://music.163.com/"></a></li>                        
                        </ul>
                    </div>
                </div>
            </div>
        );
    }
}

export default Head;